.about { 
    @include row-flex();
    // display: flex;
    height: calc(100vh - 21.4vh - 67px);
    justify-content: space-between;
    position: relative;

    .description {
        @include description;
        width: 540px;

        .title {
            
            margin-bottom: 60px;
        }

        .text {
            margin-top: 0;
            margin-bottom: 211px;
        }

    }

    .viwe {
        height: 115vh;
        left: 45%;
        top: -240px;
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 43.2%;
        background: #0C1723;
        border-radius: 60px;
        border: 25px solid #EEEEEE;
        border-bottom: none;
        z-index: -1;

        .top_of_viwe {

            border-bottom: 2px solid #242E39;     
            padding-bottom: 40px;

            .head {
                margin-top: 40px;
                position: relative;
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                color: white;
                height: 100px;
                width: 420px;
                box-shadow: 0px -10px 28px rgba(0, 0, 0, 0.06);
                border-radius: 28px;
                background: no-repeat url('../img/steve.png'), rgba(255, 255, 255, 0.05);
                background-position: 20px center;

                &:after {
                    content: "";
                    position: absolute;
                    bottom: 25px;
                    left: 95px;
                    background: url('../img/svg/stars.svg');
                    width: 105px;
                    height: 15px;
                }
                
                .steve_name {
                    padding-left: 70px;
                    font-size: 20px;
                    line-height: 30px;
                    width: 190px;
                    height: 50px;
                    border-right: 2px solid rgba(255, 255, 255, 0.1);
                }

                .per {
                    text-transform: uppercase;
                    font-size: 20px;

                    p {
                        color: #9297A2;
                        font-size: 14px;
                        text-transform: none;
                        margin: 0;
                    }

                }
            }
        }

        .chat {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin-top: 10px;
            padding: 0 35px;
            color: white;
            font-size: 16px;
            font-weight: 300;
            overflow: hidden;

            li {
                margin-top: 30px;
                padding: 10px;
                border-radius: 17.75px;
                display: inline-block;
 
              
                &:nth-child(1) {
                    animation-delay: 0.5s;
                }
                &:nth-child(2) {
                    animation-delay: 1.5s;
                }
                &:nth-child(3) {
                    transform: translateX(-140%);

                    animation-delay: 3s;
                }
                &:nth-child(4) {
                    animation-delay: 4.5s;
                }
                &:nth-child(5) {
                    animation-delay: 6s;
                }
                &:nth-child(6) {
                    animation-delay: 7.5s;
                }
                &:nth-child(7) {
                    transform: translateX(120px);
                    animation-delay: 8.8s;
                    animation-duration: 0.7s;
                }
            }
            
            .user {
                transform: translateX(112%);

                animation: 1s ease-out 0s 1 chat_right;
                animation-fill-mode: forwards;  
                background: #0072FF;
                display: inline-block;
                   
            }
            
            .steve {
                transform: translateX(-110%);     
                animation: 1s ease-out 0s 1 chat_left; 
                animation-fill-mode: forwards;  
                background: rgba(255, 255, 255, 0.05);
            }
        }
    }
}

