            //     color: #0072FF;
            //     background-position: 10px 1px;        
            //     transition: all 0.3s ease-out;

            // }
        }

    }


    .viwe {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 10vh;
        position: absolute;
        left: 45%;
        top:0;
        width: 43.2%;
        height: 84vh;
        border-radius: 0px 0px 80px 80px;
        border: 25px solid #19232F;
        border-top: none;
        overflow: hidden;

        .viwe__title {
            padding: 3.6vh 28%;
            font-weight: bold;
            font-size: 28px;
            border-bottom: solid 1px #242E39;
          
        }

        .featches {
            padding-top: 8.6vh;
            display: flex;
            flex-direction: column;
            align-items: center;
            
        

            .feache__item {
                color: #000;
                margin-bottom: 4.9vh;
                position: relative;
                border-radius: 28px;
                padding: 27px 0 27px 100px;
                width: 87.3%;
          
                &:nth-child(1){
                    background: no-repeat url("../img/ic_1.png"), #FFFFFF;
                    background-position: 25px center;

             

                    &::before {
                        content: "1";
                        @include feach_icon;
                    }
                }
                &:nth-child(2){
                    background: no-repeat url("../img/ic_2.png"), #FFFFFF;
                    background-position: 25px center;

                    &::before {
                        content: "2";
                        @include feach_icon;
                    }
                }
                &:nth-child(3){
                    background: no-repeat url("../img/ic_3.png"), #FFFFFF;
                    background-position: 25px center;

                    &::before {
                        content: "3";
                        @include feach_icon;
                    }
                }



                h4 {
                    font-size: 20px;
                }

                p {
                    margin: 10px 0 0 0;
                    font-size: 14px;
                    color: #9297A2;
                }
            }

        }
    }
}

