@keyframes chat_left {
    0% {
      transform: translateX(-110%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes chat_right {
    0% {
   
    transform: translateX(110%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes burger {
    0% {
   
    transform: translateX(100%);
    }
    100% {
      transform: translateX(0);
    }
  }

  .featchest_animation {
      &:nth-child(1) {
        animation: 1s ease-out 0.3s 1 chat_left;
        transform: translateX(-115%); 
        animation-fill-mode: forwards; 
      }
      &:nth-child(2) {
        animation: 1s ease-out 1.3s 1 chat_right;
        transform: translateX(115%); 
        animation-fill-mode: forwards; 
      }
      &:nth-child(3) {
        animation: 1s ease-out 2.6s 1 chat_left;
        transform: translateX(-115%);  
        animation-fill-mode: forwards;
      }
  }


