.suport {
    box-sizing: border-box;
    // height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: no-repeat url("../img/Suport_bg.png");
    background-position:  580px ;
    background-size: contain;
    // flex-wrap: nowrap;
      
    .title {
        font-size: 48px;
      }
    .content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .description {
            @include description();
            width: 443px;

            .text {
                color: #000;
                font-size: 18px;
                line-height: 24px;
                font-weight: 300;
      
                a {
                  color: #0072FF;
                  &:hover {
                      text-decoration-line: underline;
                  }
              }
            }
          }
      
          .viwe {
              width: 540px;
                margin-left: 215px;
              .suport_list {
                  .item {
                    box-sizing: border-box;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    width: 420px;
                    height: 100px;
                    border-radius: 28px;
                    margin-bottom: 30px;
                    padding-left: 25px;
                    background: no-repeat url("../img/svg/Suport_link_arr.svg"), #FFFFFF;
                    background-position: 380px center;


                    h4 {
                        margin-bottom: 10px;
                    }
                     p {
                        margin: 0;
                        font-size: 14px;
                        line-height: 22px;
                        color: #9297A2;
                        font-size: 300;
                     }
                  }
              }
      
          }

    }


}