#join_to_us {
    background: #F2F2F2;
}

.join_to_us {
    // height: 73.7vh;

    @include section;

    padding-top: 11vh;
    justify-content: space-between;

    .description {
        @include description;

        .pro_title {
            line-height: 100px;
            margin-bottom: 30px;
        }

        .pro_text {
            font-size: 20px;
            line-height: 34px;
            font-weight: 300;
            margin-bottom: 37px;

            p {
                margin-bottom: 40px;
            }
        }

        .btn {
            color: white;
            line-height: 60px;
            text-align: center;
            width: 300px;
            height: 60px;
            background: linear-gradient(0deg, #050B11, #050B11);
            box-shadow: 0px 2px 0px rgba(255, 255, 255, 0.0996943);
            border-radius: 30px;
            font-size: 22px;
            

            &:hover {
                color: #0072FF;
                transition: color 0.3s;
            }
        }
    }

    .viwe {
        width: 50%;
        display: flex;
        justify-content: center;

        .action {
            width: 66.8%;

            .action_item {
                position: relative;
                color: #9297A2;
                background: #fff;
                margin-bottom: 30px;
                border-radius: 28px;
                padding: 22px 0 26px 80px;

                &:nth-child(1) {
                    &::before {
                        content: "1";

                        @include action_item;
                    }
                }

                &:nth-child(2) {
                    ul {
                        padding-left: 15px;
                        list-style-type: disc;

                        li {
                            font-size: 14px;
                            line-height: 22px;
                            font-weight: 300;
                            margin: 0;
                        }
                    }

                    &::before {
                        content: "2";

                        @include action_item;
                    }
                }

                &:nth-child(3) {
                    p {
                        a {
                            color: #0072FF;

                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }

                    &::before {
                        content: "3";

                        @include action_item;
                    }
                }

                h3 {
                    font-size: 20px;
                    line-height: 22px;
                    color: #000;
                    margin: 5px 0;
                    font-weight: 500;
                }

                h4 {
                    font-size: 14px;
                    line-height: 22px;
                    margin: 0;
                    font-weight: 400;
                }

                p {
                    font-size: 14px;
                    line-height: 22px;
                    font-weight: 300;
                    margin: 0;
                }
            }
        }
    }
}

.hom_you_can_work,
.how_earn {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    .pro_title {
        line-height: 100px;
        padding: 120px 0 60px 0;
        border-bottom: 1px solid #E5E5E5;
    }

    .benefits__list {
        padding-top: 60px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        flex-wrap: wrap;

        .benefit__item {
            width: 540px;
            padding-bottom: 30px;
            position: relative;

            h3 {
                font-size: 30px;
                line-height: 35px;
                padding-left: 120px;
            }

            p {
                font-size: 20px;
                line-height: 34px;
                font-weight: 300;
                color: #9297A2;
            }

            &:nth-child(1) {
                &::before {
                    content: "✏️";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(2) {
                &::before {
                    content: "👍 ";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(3) {
                &::before {
                    content: "🔥";

                    @include hyvw__item_icon;
                }
            }

            &:nth-child(4) {
                &::before {
                    content: "❤";
                    color: red;

                    @include hyvw__item_icon;
                }
            }

            &:last-child {
                width: 100%;

                div {
                    padding-left: 120px;

                    p {
                        color: #000;
                        font-size: 18px;
                        line-height: 22px;
                        margin: 0;
                    }
                }

                &::before {
                    content: "👻";

                    @include hyvw__item_icon;

                    border: solid 20px rgba(252, 176, 71, 0.1);
                    height: 90px;
                    line-height: 50px;
                    border-radius: 38px;
                }
            }
        }
    }
}

.how_earn .benefits__list .benefit__item {
    &:nth-child(1) {
        &::before {
            content: "💸";
        }
    }

    &:nth-child(2) {
        &::before {
            content: "💰";
        }
    }

    &:last-child {
        &::before {
            content: "";
            background: no-repeat url("../img/cash_symbol.png");
        }
    }
}

#receive_requests,
#let_join {
    margin-top: 11vh;
    background: no-repeat url("../img/how_to_resive_request.png"), linear-gradient(180deg, #0C1723 64.05%, rgba(0, 0, 0, 0.5) 125.01%), #0C1723;;
    background-size: contain;
    background-position: 28%;
    height: 80.3vh;
    color: white;
}

.receive_requests {
    @include section;
    height: 100%;
    padding-top: 13.6vh;

    .viwe {
        width: 50%;
        height: 100%;
    }

    .description {
       @include description();
       max-width: 540px;

       .pro_title {
        font-weight: 900;
        font-size: 64px;
        line-height: 75px;
        margin-bottom: 30px;
       }

       ol {
        list-style-type:  decimal;
        font-size: 24px;
        line-height: 48px;
        padding-left: 20px;
       }

       .pro_text {
        font-weight: 300;
        font-size: 20px;
        line-height: 32px;
        color: #9297A2;
        margin: 0;

        a {
            color: #0072FF;

            &:hover {
                text-decoration: underline;
            }
        }
       }
    }
}

#let_join {
    background: linear-gradient(180deg, #0A121A 0%, #000000 97.44%);
    margin-top: 0;
    padding-top: 11vh;

    .let_join {
        display: flex;
        flex-direction: column;

        .let_join__title{
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            height: 100px;
            padding-bottom: 60px;
            border-bottom: 2px solid rgba(255, 255, 255, 0.1); 

            .socials {
                display: flex;
                justify-content: space-between;
                width: 132px;  
            }
        }
        .description {
            padding-top: 60px;

            .join_to_us__tags {
                padding-top: 48px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                
                .tags__description {
                    max-width: 540px;
                    font-size: 20px;
                    line-height: 34px;
                    color: #9297A2;
                }

                .tags__list {
                    width: 540px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;

                    .tag__item {
                      font-size: 16px;
                      font-weight: 400;
                      height: 20px;
                      text-align: center;
                      margin: 0 30px 30px 0;
                      background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #000000;
                      border-radius: 8px;
                      padding: 10px;

                      &:last-child {
                        background: none;
                        cursor: pointer;
                      }
                    }
                }
               
            }
            .btn {
              position: relative;
              margin-top: 60px;
              background: linear-gradient(0deg, #FFFFFF, #FFFFFF);
              border-radius: 40px;
              width: 420px;
              height: 80px;
             
              a {
                font-size: 26px;
                display: inline-block;
                position: absolute;
                line-height: 80px;
                left: 11%;
                color: #000;
                transition: all 0.3s;
                

                  &:hover {
                    color: #0072FF;
                    font-size: 28px;
                    left: 9.5%;
                    transition: all 0.3s;
                }
              
              }
            }
        }
    }

}

