.burger_menue {
  display: block;
  position: relative;
  top: 20px;
  left: 30px;
  z-index: 1;
 
    input {
        display: block;
        width: 36px;
        height: 35px;
        position: absolute;
        top: -7px;
        left: -5px;
        cursor: pointer;
        opacity: 0; 
        z-index: 2;

        &:checked ~ span {
            opacity: 1;
            transform: rotate(45deg) translate(-2px, -1px);
            background: #fff;
        }

        &:checked ~ span:nth-last-child(3) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:checked ~ span:nth-last-child(2){
            transform: rotate(-45deg) translate(6px, -6px);
       
        }

        &:checked ~ .burger_menu__links {
            visibility: visible;
            transform: translateX(0);
            transition: transform 0.9s cubic-bezier(0.07,0.2,0.05,1.0);
        }


    }

    span {
        display: block;
        width: 35px;
        height: 4px;
        margin-bottom: 10px;
        position: relative; 
        background: #000;
        border-radius: 3px; 
        z-index: 1;
        transform-origin: 4px 0px;      
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;
        
        &:first-child {
            transform-origin: 0% 0%;
        }

        &:nth-last-child(2) {
            transform-origin: 0% 100%;
          
        }
        
      }

      .burger_menu__links {
        color: #fff;
        position: fixed;
        top: 0;
        right: 0;
        width: 54vw;
        height: 100vh;
        margin: 0;
        visibility: visible;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #0C1723;
        transform: translateX(100%);
        transition: transform 0.5s linear;

        a{
            color:white;
        }
            .burger_menu__navigation_link {
                position: absolute;
                top: 83px;
                left: 20%;

                li {
                    text-align: center;
                    padding: 10px 20px ;
                    margin: 29px 0 ;
                    a {
                        font-size: 20px;
                        font-weight: 500;

                        &:hover {
                            color: #0072FF;
                            transition: color 0.3s;
                        }
                    }
                }
            }
      }

      .burger_menu__socials {
        height: 36px ;
        width: 40%;
        max-width: 144px;
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 18.5vh;
        left: 20%;
      }

      .burger_menu__foote_text {
          position: absolute;
          bottom: 69px;
          font-size: 16px;
          font-weight: 300;
          left:10%;
          a:hover {
              color: #0072FF;
              transition: color 0.3s;
          }
      }

}
