.footer {
    display: flex;
    flex-direction: column;
    
    .footer__navigation {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 40px;
    }

    .footer__navigation_link ul {
        display: flex;
        flex-direction: row;
        width: 542px;
        
        
        li {
            padding: 1.5%;
        }
        li:first-child {
            padding-left: 0;
        }
        
        li a{
            font-size: 18px;
            word-wrap: normal;
            font-weight: 300;
        }
    }

    .footer__socials {
        height: 36px;
        max-width: 210px;
 
        a {
            padding: 25px;
        }
    }

    .footer_copyright {
        font-size: 14px;
    }

}