@mixin feach_icon {
    position: absolute;
    left: -3px;
    top: -10px;
    display: inline-block;
    background: red;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    color: white;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
}

@mixin description {
    width: 50%;
    display: flex;
    flex-direction: column;
}

@mixin section {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
}

@mixin action_item {
    display: inline-block;
    width: 50px;
    height: 50px;
    color: #fff;
    background: #000;
    font-weight: bold;
    font-size: 26px;
    border-radius: 25px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    left: 15px;
    top: 25px;
}

@mixin hyvw__item_icon {
    display: inline-block;
    box-sizing: border-box;
    position: absolute;
    top: -30px;
    left: 0;
    font-size: 40px;
    width: 90px;
    height: 80px;
    border: 10px solid #F2F2F2;
    border-radius: 28px;
    line-height: 60px;
    text-align: center;
}
