.doc {
  @include section();
  flex-direction: column;
  width: 1140px;
  margin-bottom: 120px;

  .doc__title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      h1 {
       font-size: 48px;
      }

      .policy {
        font-weight: bold;
        font-size: 28px;
        position: relative;
        top: 20px;
        // z-index: 10;


        .selected_policy {
          padding-right: 40px;
          background: no-repeat url("../img/svg/Arr_for_select.svg");
          background-position: right 10px;
          cursor: pointer;

        }

        .conteiner_policy{
          width: 333px;
          position: absolute;
          align-items: center;
          justify-content: center;
          top: 56px;
          right: 0;
          box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.202502);
          background: #fff;
          border-radius: 15px;
          

        .policy_item {
          text-align: center;
          
          padding: 39px 0;
          // width: 100%;
          cursor: pointer;
          

          &:hover {
              color: #0072FF;
              transition: color 0.3s;
          }

        }
        .policy_item_input {
          display: none;
        }
        }
      }











  }
  
  .comment { 
    font-size: 22px;
    line-height: 34px;
    color: #9297A2;
    font-weight: 300;
  }

  .doc__text {
    font-size: 18px;
    line-height: 24px;
    font-weight: 300;
  }

}